.productdisplay {
  display: flex;
  margin: 0px 170px;
}

.productdisplay-left {
  display: flex;
  gap: 17px;
}

.productdisplay-img-list {
  display: flex;
  flex-direction: column;
  gap: 17px;
}
.productdisplay-img-list img {
  height: 163px;
}
.productdisplay-main-img {
  width: 586px;
}

.productdisplay-right {
  margin: 0px 70px;
  display: flex;
  flex-direction: column;
}

.productdisplay-right h1 {
  color: rgb(56, 56, 56);
  font-size: 40px;
  font-weight: 700;
}

.productdisplay-right-stars {
  display: flex;
  align-items: center;
  margin-top: 13px;
  gap: 5px;
  color: grey;
  font-size: 16px;
}

.productdisplay-right-prices {
  display: flex;
  margin: 20px 0px;
  gap: 30px;
  font-size: 24px;
  font-weight: 700;
}

.productdisplay-right-price-old {
  color: grey;
  text-decoration: line-through;
}

.productdisplay-right-size h1 {
  margin-top: 55px;
  color: rgb(44, 44, 44);
  font-size: 20px;
  font-weight: 600;
}
.productdisplay-right-sizes {
  display: flex;
  margin: 30px 0px;
  gap: 20px;
}

.productdisplay-right-sizes div {
  padding: 18px 24px;
  background: rgba(240, 248, 255, 0.798);
  border: 1px solid rgba(128, 128, 128, 0.511);
  border-radius: 5px;
  cursor: pointer;
}

.productdisplay-right button {
  padding: 20px 40px;
  margin-bottom: 80px;
  width: 200px;
  font-size: 16px;
  font-weight: 600;
  color: white;
  background: red;
  border: none;
  outline: none;
  cursor: pointer;
  border: 1px solid red;
}
.productdisplay-right button:hover {
  background: rgba(255, 255, 255, 0.808);
  color: rgb(0, 0, 0);
  border: 1px solid black;
}

.productdisplay-right-category {
  margin-top: 5px;
}
.productdisplay-right-category span {
  font-weight: 600;
}

@media (max-width: 1280px) {
  .productdisplay {
    margin: 0px 60px;
  }
  .productdisplay-left {
    gap: 10px;
  }
  .productdisplay-img-list {
    gap: 10px;
  }
  .productdisplay-img-list img {
    height: 120px;
  }
  .productdisplay-main-img {
    width: auto;
    height: 510px;
  }
  .productdisplay-right {
    margin: 0px 30px;
  }
  .productdisplay-right h1 {
    font-size: 22px;
  }
  .productdisplay-right-stars {
    gap: 3px;
    font-size: 13px;
  }
  .productdisplay-right-prices {
    margin: 10px 0px;
    font-size: 18px;
  }
  .productdisplay-right-description {
    font-size: 13px;
  }
  .productdisplay-right-size h1 {
    margin-top: 20px;
    font-size: 20px;
  }
  .productdisplay-right-sizes div {
    padding: 14px 20px;
  }
  .productdisplay-right button {
    width: 150px;
    padding: 15px 0px;
    margin-bottom: 20px;
  }
  .productdisplay-right-category {
    margin-top: 5px;
    font-size: 14px;
  }
}
@media (max-width: 1024px) {
  .productdisplay {
    margin: 0px 30px;
  }
  .productdisplay-left {
    gap: 5px;
  }

  .productdisplay-img-list img {
    height: 80px;
  }
  .productdisplay-main-img {
    height: 350px;
  }

  .productdisplay-right h1 {
    font-size: 18px;
  }
  .productdisplay-right-stars {
    font-size: 12px;
  }
  .productdisplay-right-stars img {
    width: 15px;
  }

  .productdisplay-right-description {
    font-size: 12px;
  }
  .productdisplay-right-sizes {
    margin-top: 10px;
  }
  .productdisplay-right-sizes div {
    padding: 10px 16px;
    font-size: 12px;
  }
  .productdisplay-right button {
    width: 120px;
    padding: 10px 0px;
    margin-bottom: 10px;
    font-size: 14px;
  }
  .productdisplay-right-category {
    font-size: 12px;
  }
}
@media (max-width: 800px) {
  .productdisplay {
    margin: 0px 10px;
  }
  .productdisplay-left {
    gap: 0px;
    flex-direction: column-reverse;
    align-items: center;
  }
  .productdisplay-img-list {
    flex-direction: row;
    gap: 6px;
  }
  .productdisplay-img-list img {
    height: 70px;
  }
  .productdisplay-main-img {
    height: 300px;
  }

  .productdisplay-right h1 {
    font-size: 14px;
  }

  .productdisplay-right-stars img {
    width: 13px;
  }

  .productdisplay-right-description {
    font-size: 10px;
  }
  .productdisplay-right-size h1 {
    margin-top: 10px;
  }
  .productdisplay-right-sizes {
    margin-top: 10px;
    gap: 5px;
  }
  .productdisplay-right-sizes div {
    padding: 4px 11px;
  }
  .productdisplay-right button {
    width: 100px;

    font-size: 12px;
  }
  .productdisplay-right-category {
    font-size: 10px;
  }
}

@media (max-width: 500px) {
  .productdisplay {
    flex-direction: column;
  }
  .productdisplay-left {
    gap: 10px;
    flex-direction: row;
    margin: auto;
  }
  .productdisplay-img-list {
    flex-direction: column;
    gap: 8px;
  }
  .productdisplay-img-list img {
    height: 75px;
  }
  .productdisplay-main-img {
    height: 330px;
  }
  .productdisplay-right {
    margin: 5px;
  }
  .productdisplay-right h1 {
    font-size: 20px;
    margin-top: 15px;
    font-weight: 500;
  }

  .productdisplay-right-stars img {
    width: 15px;
  }

  .productdisplay-right-sizes {
    gap: 10px;
    margin: 20px;
  }
  .productdisplay-right-sizes div {
    padding: 10px 16px;
    font-size: 14px;
  }
  .productdisplay-right button {
    width: 130px;
    padding: 12px 0px;
    font-size: 15px;
  }
  .productdisplay-right-category {
    font-size: 16px;
  }
}
